body {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    
}

.app {
    height: 100vh;
    background-color: #fff7db;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.content {
    background-color: #F0DB4F;
    padding: 1rem;
    border-radius: .5rem;
    width: 900px;
    max-width: 90%;
}

.content__top {
    margin-bottom: 25px;
    font-family: 'Grandstander', cursive;
    font-size: 30px;
}

.content__bottom-input {
    background-color: transparent;
    border: 2px solid #A1922E;
    outline: none;
    width: 100%;
    height: 8rem;
    resize: none;
    font-size: 30px;
    border-radius: .5rem;
}

.incorrect {
    color: red;
}

.correct {
    color: green !important;
}


.title {
    font-family: 'Grandstander', cursive;
    font-size: 40px;
    text-align: center;
    margin: 10px;
}

.result-alert {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-items: center;
    width: 650px;
    height: 250px;
    border: 1px solid #000;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.button {
    display: block;
    border: 1px solid #000;
    background-color: transparent;
    outline: none;
    padding: 10px 0px;
    cursor: pointer;
    width: 150px;
    &:hover {
        background-color: #000;
        color: #fff;
        
    }
}

.timer {
    display: flex;
    align-items: center;
    flex-direction: column;
}